import React from "react";
import { connect } from "react-redux";
import { addItem, loadUser } from "../redux/actions";

import Filters from "./Filters";

class Loader extends React.Component {
  constructor(props) {
    super(props);

    this.state = { loading_state: {
        user: 0,
        items: 0
      } 
    };

    this.props.addItem({ name: "title", cost: 10, category: "Anything...", img:"https://coding-challenge-api.aerolab.co/images/Alienware13-x2.png" });
    this.props.addItem({ name: "title2", cost: 10, category: "Anything...", img:"" });
    this.props.addItem({ name: "title3", cost: 5, category: "Anything...", img:"" });
    this.props.addItem({ name: "title4", cost: 20, category: "Anything...", img:"" });
    this.props.addItem({ name: "title5", cost: 20, category: "Anything...", img:"" });
    this.props.addItem({ name: "title6", cost: 20, category: "Anything...", img:"" });
    this.props.addItem({ name: "title7", cost: 20, category: "Anything...", img:"" });
    this.props.addItem({ name: "title8", cost: 20, category: "Anything...", img:"" });
    this.props.addItem({ name: "title9", cost: 20, category: "Anything...", img:"" });

    this.props.loadUser({ name: "John Kite", points: 2000, redeemHistory: [], createDate: new Date(1510171520852) });
    
    this.state.loading_state.user = 1;
    this.state.loading_state.items = 1;
    
  }


message(){
  switch(this.state.loading_state.user | this.state.loading_state.items){
    case 0 | 0:
      return <div class="loader alert alert-primary" role="alert">"Loading"</div>;
    case 2 | 1:
      return <div class="loader alert alert-danger" role="alert">Sorry, we can't load your user information at this moment, please try again later!</div>;
    case 1 | 2:
      return <div class="loader alert alert-danger" role="alert">Sorry, we can't load the reward list at this moment, please try again later!</div>;
    case 2 | 2:
      return <div class="loader alert alert-danger" role="alert">Sorry, we can't load the resources at this moment, please try again later!</div>;
    default: 
    return  <div className="loader container">
              <Filters  />
            </div>;
  }
}
  render() {
    return (
      this.message()
    );
  }
}

export default connect(
  null,
  { addItem, loadUser }
)(Loader);