import { LOAD_USER } from "../actionTypes";

const initialState = {
  allIds: [],
  byIds: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case LOAD_USER: {
      const { id, name, points, redeemHistory, createDate } = action.payload;
      return {
        ...state,
        allIds: [...state.allIds, id],
        byIds: {
          ...state.byIds,
          [id]: {
            name, 
            points, 
            redeemHistory, 
            createDate
          }
        }
      };
    }
    default:
      return state;
  }
}
