import { LOAD_USER, ADD_ITEM, ADD_TO_CART, SET_FILTER, SET_PAGINATION } from "./actionTypes";

let nextItemId = 0;
let nextUserId = 0;

export const addItem = ({ name, cost, category, img}) => ({
  type: ADD_ITEM,
  payload: {
    id: ++nextItemId,
    name, 
    cost, 
    category, 
    img
  }
});

export const addToCart = id => ({
  type: ADD_TO_CART,
  payload: { id }
});

export const loadUser = ({ name, points, redeemHistory, createDate }) =>({
  type: LOAD_USER,
  payload: {
    id: ++nextUserId,
    name, 
    points, 
    redeemHistory, 
    createDate
  }
});

export const setFilter = filter => ({ type: SET_FILTER, payload: { filter } });
export const setPage = page => ({ type: SET_PAGINATION, payload: { page } });
